.App {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

.wrapper {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  margin: auto;
  width: 40rem;
  align-items: center;
  border: 2px solid $secondary;
  border-radius: 1rem;
  background-color: $ligthBackground;
  box-shadow: 0.5rem 1rem 0.5rem $primary;

  @media screen and (max-width: 768px) {
    width: 25rem;
  }

  @media screen and (max-width: 480px) {
    width: 18rem;
  }
}

.contact-form {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  font-size: 1rem;
  gap: 1rem;
  width: 100%;
    
    h1 {
        color: $background;
        text-align: center;

        @media screen and (max-width: 768px) {
          font-size: 1.5rem;
        }
      }

    input[type='text'], input[type='date'], input[type='tel'], textarea {
      width:100%;
      padding: 0.8rem;
      font-style: italic;
      border: 2px solid $secondary;
      border-radius: 1rem;
      background: transparent;
      outline-color: #FFD6BA;
      font-size: 1rem;
      margin: 0.2rem;

      @media screen and (max-width: 768px) {
        font-size: 0.8rem;
      }
  }

    textarea {
      resize: vertical;
    }

    label {
      color: $buttonHover;
      font-size: 1.5rem;

      @media screen and (max-width: 768px) {
        font-size: 1rem;
      }
    }

  .send-button {
    padding: 1rem;
    text-align: center;
    font-size: 1rem;
    background-color: $background;
    border: 1px solid $background;
    border-radius: 4px;
    color: $ligthBackground;
    letter-spacing: 5px;
    text-transform: uppercase;
    cursor: pointer;
    transition: background-color 0.3s linear;
  }

  .send-button:hover {
    background-color: $buttonHover;
  }

  .send-button:active {
    background-color: $primary;
    transform:translateX(1.3);
  }

  .send-button[disabled]{
    border: 1px solid $secondary;
    background-color: $secondary;
  }

  .errorWrapper {
    display: block;
    height: 0.8rem;
  }

  .success {
    display: block;
    height: 1.5rem;
    color: $background;
    font-size: 1.5rem;
    text-align: center;

    @media screen and (max-width: 768px) {
      font-size: 1rem;
    }
  }
}

.error-message {
  color: #ff0000;
  font-size: 0.8rem;
}